'use client';

import { useState, useEffect, useCallback } from 'react';
import { getPrayerTimes, deletePrayerTime } from '@/lib/api/prayer-times';
import UpdatePrayerTimeModal from '@/components/prayer-management/UpdatePrayerTimeModal';
import type { PrayerTimeResponse, PrayersData } from '@/types/prayer-times';

/* ── Constants ── */

const PRAYER_NAMES: (keyof PrayersData)[] = ['fajr', 'sunrise', 'zuhr', 'asr', 'maghrib', 'isha'];

const PRAYER_LABELS: Record<keyof PrayersData, string> = {
    fajr: 'Fajr',
    sunrise: 'Sunrise',
    zuhr: 'Zuhr',
    asr: 'Asr',
    maghrib: 'Maghrib',
    isha: 'Isha',
};

const MONTHS = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December',
];

const MONTHS_SHORT = [
    'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
    'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',
];

const DAYS_FULL = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];

/* ── Helpers ── */

function formatTime12h(time24: string | undefined): string {
    if (!time24) return '—';
    const [hoursStr, minutesStr] = time24.split(':');
    let hours = parseInt(hoursStr, 10);
    const minutes = minutesStr;
    const ampm = hours >= 12 ? 'PM' : 'AM';
    if (hours > 12) hours -= 12;
    if (hours === 0) hours = 12;
    return `${hours}:${minutes} ${ampm}`;
}

/** Returns time+ampm parts for Athan superscript, e.g. { time: "5:50", ampm: "AM" } */
function formatAthanParts(time24: string | undefined): { time: string; ampm: string } | null {
    if (!time24) return null;
    const [hoursStr, minutesStr] = time24.split(':');
    let hours = parseInt(hoursStr, 10);
    const minutes = minutesStr;
    const ampm = hours >= 12 ? 'PM' : 'AM';
    if (hours > 12) hours -= 12;
    if (hours === 0) hours = 12;
    return { time: `${hours}:${minutes}`, ampm };
}

function getDateParts(dateStr: string) {
    const d = new Date(dateStr + 'T00:00:00');
    return {
        dayFull: DAYS_FULL[d.getDay()],
        dayNum: d.getDate(),
        isFriday: d.getDay() === 5,
        monthShort: MONTHS_SHORT[d.getMonth()],
    };
}

/* ── Shared arrow SVG (Figma: "Component 1") ── */

function ChevronRight({ className = '' }: { className?: string }) {
    return (
        <svg width="20" height="20" viewBox="0 0 20 20" fill="none" className={className}>
            <path d="M7.5 5L12.5 10L7.5 15" stroke="var(--brand)" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
    );
}

/* ═══════════════════════════════════════════════════
   Main Page
   ═══════════════════════════════════════════════════ */

export default function PrayerManagementPage() {
    const [prayerTimes, setPrayerTimes] = useState<PrayerTimeResponse[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [showModal, setShowModal] = useState(false);
    const [editingPrayerTime, setEditingPrayerTime] = useState<PrayerTimeResponse | null>(null);

    const [currentMonth, setCurrentMonth] = useState(new Date().getMonth());
    const [currentYear, setCurrentYear] = useState(new Date().getFullYear());

    const todayStr = new Date().toISOString().split('T')[0];
    const todayPrayerTime = prayerTimes.find(pt => pt.date === todayStr);

    /* ── data fetching ── */
    const fetchPrayerTimes = useCallback(async () => {
        setLoading(true);
        setError(null);
        try {
            const startDate = `${currentYear}-${String(currentMonth + 1).padStart(2, '0')}-01`;
            const lastDay = new Date(currentYear, currentMonth + 1, 0).getDate();
            const endDate = `${currentYear}-${String(currentMonth + 1).padStart(2, '0')}-${String(lastDay).padStart(2, '0')}`;
            const result = await getPrayerTimes({ startDate, endDate, size: 31 });
            setPrayerTimes(result.content);
        } catch (err) {
            setError(err instanceof Error ? err.message : 'Failed to load prayer times');
        } finally {
            setLoading(false);
        }
    }, [currentMonth, currentYear]);

    useEffect(() => { fetchPrayerTimes(); }, [fetchPrayerTimes]);

    /* ── navigation ── */
    const handlePrevMonth = () => {
        if (currentMonth === 0) { setCurrentMonth(11); setCurrentYear(y => y - 1); }
        else { setCurrentMonth(m => m - 1); }
    };
    const handleNextMonth = () => {
        if (currentMonth === 11) { setCurrentMonth(0); setCurrentYear(y => y + 1); }
        else { setCurrentMonth(m => m + 1); }
    };

    /* ── actions ── */
    const handleDelete = async (id: string) => {
        if (!confirm('Are you sure you want to delete this prayer time entry?')) return;
        try { await deletePrayerTime(id); fetchPrayerTimes(); }
        catch { alert('Failed to delete prayer time'); }
    };
    const handleEdit = (pt: PrayerTimeResponse) => { setEditingPrayerTime(pt); setShowModal(true); };
    const handleAddNew = () => { setEditingPrayerTime(null); setShowModal(true); };
    const handleModalClose = () => { setShowModal(false); setEditingPrayerTime(null); };
    const handleModalSuccess = () => { handleModalClose(); fetchPrayerTimes(); };

    /* ── determine active prayer ── */
    const getActivePrayer = (): keyof PrayersData | null => {
        if (!todayPrayerTime) return null;
        const now = new Date();
        const currentMin = now.getHours() * 60 + now.getMinutes();
        const order: (keyof PrayersData)[] = ['fajr', 'sunrise', 'zuhr', 'asr', 'maghrib', 'isha'];
        let active: keyof PrayersData | null = null;
        for (const p of order) {
            const t = todayPrayerTime.prayers[p]?.jamah || todayPrayerTime.prayers[p]?.athan;
            if (t) {
                const [h, m] = t.split(':').map(Number);
                if (currentMin >= h * 60 + m) active = p;
            }
        }
        return active;
    };
    const activePrayer = getActivePrayer();

    /* ── hijri range for month header ── */
    const firstHijri = prayerTimes.length > 0 ? prayerTimes[0]?.hijriDate : null;
    const lastHijri = prayerTimes.length > 1 ? prayerTimes[prayerTimes.length - 1]?.hijriDate : null;
    const hijriRange = firstHijri && lastHijri && firstHijri !== lastHijri
        ? `${firstHijri} - ${lastHijri}`
        : firstHijri || '';

    /* ═══════════════════════════════════════════════════
       RENDER
       ═══════════════════════════════════════════════════ */
    return (
        <div className="flex flex-col gap-6">

            {/* ─────────────── Page Header ─────────────── */}
            <div className="flex items-center justify-between">
                <h1 className="font-urbanist font-bold text-[24px] text-[#1f1f1f] leading-normal">
                    Prayer Management
                </h1>
                <button
                    onClick={handleAddNew}
                    className="flex items-center gap-2.5 h-[44px] px-6 bg-[var(--brand)] text-white rounded-[12px]
                               font-inter font-medium text-[16px] hover:opacity-90 transition-opacity cursor-pointer"
                >
                    {/* Calendar-Add icon */}
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
                        <rect x="2" y="3" width="16" height="14" rx="2" stroke="currentColor" strokeWidth="1.5" />
                        <path d="M2 7.5h16" stroke="currentColor" strokeWidth="1.5" />
                        <path d="M6 1.5v3M14 1.5v3" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
                        <path d="M10 10.5v4M8 12.5h4" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
                    </svg>
                    Add Prayer Time
                </button>
            </div>

            {/* ─────────────── Date Section ─────────────── */}
            <div className="flex flex-col gap-3 items-center w-full">

                {/* Date Banner */}
                <div className="flex items-center justify-between w-full bg-[var(--brand-05)] px-4 py-1.5 rounded-sm">
                    {/* Previous arrow */}
                    <button className="border border-[var(--brand-50)] rounded-[8px] p-1.5 flex items-center justify-center hover:bg-white/50 transition-colors cursor-pointer">
                        <ChevronRight className="rotate-180" />
                    </button>

                    {/* Center date text */}
                    <div className="flex flex-col items-center gap-0.5">
                        <p className="font-urbanist font-semibold text-[24px] text-[var(--grey-800)] leading-normal">
                            {new Date().toLocaleDateString('en-US', { weekday: 'long', month: 'long', day: 'numeric', year: 'numeric' })}
                        </p>
                        {todayPrayerTime?.hijriDate && (
                            <p className="font-urbanist font-semibold text-[20px] text-[var(--brand)] leading-normal">
                                {todayPrayerTime.hijriDate}
                            </p>
                        )}
                    </div>

                    {/* Next arrow */}
                    <button className="border border-[var(--brand-50)] rounded-[8px] p-1.5 flex items-center justify-center hover:bg-white/50 transition-colors cursor-pointer">
                        <ChevronRight />
                    </button>
                </div>

                {/* Prayer Time Cards */}
                <div className="flex items-center justify-between w-full py-3">
                    {PRAYER_NAMES.map((prayer) => {
                        const isActive = activePrayer === prayer;
                        const data = todayPrayerTime?.prayers?.[prayer];
                        const athanParts = formatAthanParts(data?.athan);
                        const isSunrise = prayer === 'sunrise';

                        return (
                            <div
                                key={prayer}
                                className={`
                                    flex flex-col items-center justify-center gap-2
                                    p-6 rounded-[12px] border
                                    transition-all duration-200
                                    ${isActive
                                        ? 'bg-[var(--brand)] border-[var(--brand-10)] text-white'
                                        : 'bg-[var(--brand-05)] border-[var(--brand-10)]'
                                    }
                                `}
                            >
                                {/* Prayer name */}
                                <p className={`font-urbanist font-normal text-[26px] uppercase tracking-[-0.14px] leading-normal ${isActive ? 'text-white' : 'text-[var(--brand)]'}`}>
                                    {PRAYER_LABELS[prayer]}
                                </p>

                                {/* Jamah / Main time */}
                                <p className={`font-volkhov font-bold text-[26px] leading-normal ${isActive ? 'text-white' : 'text-[var(--brand)]'}`}>
                                    {data ? formatTime12h(data.jamah || data.athan) : '—'}
                                </p>

                                {/* Athan time with superscript AM */}
                                <p className={`font-urbanist font-medium uppercase text-center leading-none ${isSunrise ? 'opacity-0' : ''} ${isActive ? 'text-white' : 'text-[#578d6d]'}`}>
                                    {athanParts ? (
                                        <>
                                            <span className="text-[22px]">Athan {athanParts.time}</span>
                                            <span className="text-[14px]">{athanParts.ampm}</span>
                                        </>
                                    ) : (
                                        <span className="text-[22px]">—</span>
                                    )}
                                </p>
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* ─────────────── Timetable Section ─────────────── */}
            <div className="flex flex-col gap-4 w-full">

                {/* Month Header Bar */}
                <div className="flex items-center justify-between bg-[var(--brand-05)] h-[67px] px-4 py-1.5">
                    {/* Back arrow */}
                    <button
                        onClick={handlePrevMonth}
                        className="border border-[var(--brand-50)] rounded-[8px] p-1.5 flex items-center justify-center hover:bg-white/50 transition-colors cursor-pointer"
                    >
                        <ChevronRight className="rotate-180" />
                    </button>

                    {/* Center: month + hijri */}
                    <div className="flex flex-col items-center gap-0.5">
                        <p className="font-urbanist font-semibold text-[24px] text-[var(--grey-800)] leading-normal">
                            {MONTHS[currentMonth]} {currentYear}
                        </p>
                        {hijriRange && (
                            <p className="font-urbanist font-semibold text-[20px] text-[var(--brand)] leading-normal">
                                {hijriRange}
                            </p>
                        )}
                    </div>

                    {/* Forward arrow */}
                    <button
                        onClick={handleNextMonth}
                        className="border border-[var(--brand-50)] rounded-[8px] p-1.5 flex items-center justify-center hover:bg-white/50 transition-colors cursor-pointer"
                    >
                        <ChevronRight />
                    </button>
                </div>

                {/* Table Content */}
                {loading ? (
                    <div className="flex items-center justify-center py-20">
                        <div className="w-8 h-8 border-3 border-[var(--brand)] border-t-transparent rounded-full animate-spin" />
                    </div>
                ) : error ? (
                    <div className="text-center py-20">
                        <p className="font-urbanist text-[var(--error)] text-[16px]">{error}</p>
                        <button onClick={fetchPrayerTimes} className="mt-4 px-6 py-2.5 bg-[var(--brand)] text-white rounded-[12px] font-inter font-medium text-[16px] cursor-pointer">Retry</button>
                    </div>
                ) : prayerTimes.length === 0 ? (
                    <div className="text-center py-20">
                        <p className="font-urbanist text-[var(--neutral-500)] text-[16px]">
                            No prayer times found for {MONTHS[currentMonth]} {currentYear}
                        </p>
                        <button onClick={handleAddNew} className="mt-4 px-6 py-2.5 bg-[var(--brand)] text-white rounded-[12px] font-inter font-medium text-[16px] cursor-pointer">Add Prayer Times</button>
                    </div>
                ) : (
                    <div className="flex flex-col gap-0.5">
                        {/* ── Table Header ── */}
                        <div className="flex h-[48px]">
                            <div className="bg-[var(--table-white)] border-y border-[var(--border-01)] flex items-center px-4 py-3.5 w-[354px] shrink-0">
                                <p className="font-urbanist font-medium text-[16px] text-[#667085] uppercase">Day</p>
                            </div>
                            {PRAYER_NAMES.map((p) => (
                                <div key={p} className="bg-[var(--table-white)] border-y border-[var(--border-01)] flex flex-1 items-center px-4 py-3.5 min-w-0">
                                    <p className="font-urbanist font-medium text-[16px] text-[#667085] uppercase">{PRAYER_LABELS[p]}</p>
                                </div>
                            ))}
                        </div>

                        {/* ── Table Rows ── */}
                        {prayerTimes.map((pt) => {
                            const { dayFull, dayNum, monthShort } = getDateParts(pt.date);
                            const isToday = pt.date === todayStr;
                            const textColor = isToday ? 'text-[var(--grey-800)]' : 'text-[#666d80]';

                            return (
                                <div key={pt.id} className="group">
                                    {/* ── Begins Row ── */}
                                    <div className="flex h-[52px] bg-white">
                                        {/* Day + Begins label */}
                                        <div className="flex shrink-0 w-[354px]">
                                            <div className="w-[188px] flex items-center px-4 overflow-hidden">
                                                <p className={`font-urbanist font-medium text-[16px] ${textColor} leading-normal whitespace-nowrap`}>
                                                    {monthShort} {dayNum}, {dayFull}
                                                </p>
                                            </div>
                                            <div className="w-[145px] flex items-center justify-end px-4 overflow-hidden">
                                                <p className={`font-urbanist font-medium text-[20px] ${textColor} leading-normal`}>
                                                    Begins
                                                </p>
                                            </div>
                                        </div>
                                        {/* Prayer times */}
                                        {PRAYER_NAMES.map((prayer) => {
                                            const pData = pt.prayers[prayer];
                                            return (
                                                <div key={prayer} className="flex flex-1 items-center px-4 min-w-0 overflow-hidden">
                                                    <p className={`font-urbanist font-medium text-[24px] ${textColor} leading-normal`}>
                                                        {formatTime12h(pData?.athan)}
                                                    </p>
                                                </div>
                                            );
                                        })}
                                    </div>

                                    {/* ── Jama'h Row ── */}
                                    <div className={`flex h-[52px] ${isToday ? 'bg-[var(--brand)]' : 'bg-[var(--brand-10)]'}`}>
                                        {/* Hijri + Jama'h label */}
                                        <div className="flex shrink-0 w-[354px]">
                                            <div className="w-[188px] flex items-center px-4 overflow-hidden">
                                                <p className={`font-urbanist font-medium text-[16px] leading-normal whitespace-nowrap ${isToday ? 'text-white' : 'text-[var(--grey-800)]'}`}>
                                                    {pt.hijriDate || '—'}
                                                </p>
                                            </div>
                                            <div className="w-[145px] flex items-center justify-end px-4 overflow-hidden">
                                                <p className={`font-urbanist font-medium text-[20px] leading-normal ${isToday ? 'text-white' : 'text-[var(--grey-800)]'}`}>
                                                    Jama&apos;h
                                                </p>
                                            </div>
                                        </div>
                                        {/* Prayer times */}
                                        {PRAYER_NAMES.map((prayer) => {
                                            const pData = pt.prayers[prayer];
                                            const isSunrise = prayer === 'sunrise';
                                            return (
                                                <div key={prayer} className="flex flex-1 items-center px-4 min-w-0 overflow-hidden">
                                                    <p className={`font-urbanist font-medium text-[24px] leading-normal ${isSunrise ? 'opacity-0' : ''} ${isToday ? 'text-white' : 'text-[var(--grey-800)]'}`}>
                                                        {isSunrise ? '—' : formatTime12h(pData?.jamah)}
                                                    </p>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>

            {/* ─────────────── Modal ─────────────── */}
            {showModal && (
                <UpdatePrayerTimeModal
                    prayerTime={editingPrayerTime}
                    onClose={handleModalClose}
                    onSuccess={handleModalSuccess}
                />
            )}
        </div>
    );
}
