'use client';

import { useState, useEffect } from 'react';
import { createPrayerTime, updatePrayerTime } from '@/lib/api/prayer-times';
import type { PrayerTimeResponse, PrayersData, JumuahTimeEntry } from '@/types/prayer-times';

interface UpdatePrayerTimeModalProps {
    prayerTime: PrayerTimeResponse | null; // null = creating new
    onClose: () => void;
    onSuccess: () => void;
}

const EMPTY_PRAYERS: PrayersData = {
    fajr: { athan: '', jamah: '' },
    sunrise: { athan: '' },
    zuhr: { athan: '', jamah: '' },
    asr: { athan: '', jamah: '' },
    maghrib: { athan: '', jamah: '' },
    isha: { athan: '', jamah: '' },
};

const EMPTY_JUMUAH: JumuahTimeEntry = { begins: '', khutbah: '', jamah: '' };

const PRAYER_ROWS: { key: keyof PrayersData; label: string; hasJamah: boolean }[] = [
    { key: 'fajr', label: 'FAJR', hasJamah: true },
    { key: 'sunrise', label: 'SUNRISE', hasJamah: false },
    { key: 'zuhr', label: 'ZUHR', hasJamah: true },
    { key: 'asr', label: 'ASR', hasJamah: true },
    { key: 'maghrib', label: 'MAGHRIB', hasJamah: true },
    { key: 'isha', label: 'ISHA', hasJamah: true },
];

export default function UpdatePrayerTimeModal({ prayerTime, onClose, onSuccess }: UpdatePrayerTimeModalProps) {
    const isEditing = prayerTime !== null;
    const [activeTab, setActiveTab] = useState<'daily' | 'jumuah'>('daily');
    const [date, setDate] = useState(prayerTime?.date || '');
    const [hijriDate, setHijriDate] = useState(prayerTime?.hijriDate || '');
    const [prayers, setPrayers] = useState<PrayersData>(prayerTime?.prayers || EMPTY_PRAYERS);
    const [jumuahTimes, setJumuahTimes] = useState<JumuahTimeEntry[]>(
        prayerTime?.jumuahTimes || [{ ...EMPTY_JUMUAH }]
    );
    const [saving, setSaving] = useState(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const handleEsc = (e: KeyboardEvent) => { if (e.key === 'Escape') onClose(); };
        window.addEventListener('keydown', handleEsc);
        return () => window.removeEventListener('keydown', handleEsc);
    }, [onClose]);

    const handlePrayerChange = (prayer: keyof PrayersData, field: 'athan' | 'jamah', value: string) => {
        setPrayers(prev => ({ ...prev, [prayer]: { ...prev[prayer], [field]: value } }));
    };

    const handleJumuahChange = (index: number, field: keyof JumuahTimeEntry, value: string) => {
        setJumuahTimes(prev => {
            const updated = [...prev];
            updated[index] = { ...updated[index], [field]: value };
            return updated;
        });
    };

    const addJumuahSlot = () => setJumuahTimes(prev => [...prev, { ...EMPTY_JUMUAH }]);
    const removeJumuahSlot = (index: number) => {
        if (jumuahTimes.length > 1) setJumuahTimes(prev => prev.filter((_, i) => i !== index));
    };

    const handleSave = async () => {
        if (!isEditing && !date) { setError('Date is required'); return; }
        setSaving(true);
        setError(null);
        try {
            const filteredJumuah = jumuahTimes.filter(j => j.jamah || j.begins || j.khutbah);
            if (isEditing) {
                await updatePrayerTime(prayerTime.id, {
                    hijriDate: hijriDate || undefined,
                    prayers,
                    jumuahTimes: filteredJumuah.length > 0 ? filteredJumuah : undefined,
                });
            } else {
                await createPrayerTime({
                    date,
                    hijriDate: hijriDate || undefined,
                    prayers,
                    jumuahTimes: filteredJumuah.length > 0 ? filteredJumuah : undefined,
                });
            }
            onSuccess();
        } catch (err) {
            setError(err instanceof Error ? err.message : 'Failed to save prayer time');
        } finally {
            setSaving(false);
        }
    };

    return (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
            {/* Backdrop */}
            <div className="absolute inset-0 bg-black/40" onClick={onClose} />

            {/* Modal Panel — 1020px wide, 24px border-radius per Figma */}
            <div className="relative bg-white rounded-[24px] w-full max-w-[1020px] max-h-[90vh] overflow-y-auto shadow-2xl border border-[var(--border-01)]">

                {/* ── Header ── */}
                <div className="flex items-center justify-between px-6 pt-6 pb-4">
                    <h2 className="font-urbanist font-semibold text-[20px] text-[var(--grey-800)]">
                        {isEditing ? 'Update Prayer Time' : 'Add Prayer Time'}
                    </h2>
                    <button
                        onClick={onClose}
                        className="p-1.5 hover:bg-[var(--neutral-100)] rounded-full transition-colors cursor-pointer"
                    >
                        <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
                            <path d="M15 5L5 15M5 5l10 10" stroke="var(--grey-800)" strokeWidth="2" strokeLinecap="round" />
                        </svg>
                    </button>
                </div>

                {/* ── Content ── */}
                <div className="px-6 pb-2">

                    {/* Date Range */}
                    <div className="mb-5">
                        <label className="block font-urbanist font-medium text-[13px] text-[var(--neutral-500)] mb-1.5">
                            Date Range
                        </label>
                        {isEditing ? (
                            <div className="flex items-center gap-2 px-4 py-2.5 border border-[var(--border-01)] rounded-[8px] bg-[var(--neutral-100)]">
                                <svg width="16" height="16" viewBox="0 0 16 16" fill="none">
                                    <rect x="1" y="2" width="14" height="12" rx="2" stroke="var(--neutral-500)" strokeWidth="1.2" />
                                    <path d="M1 6h14" stroke="var(--neutral-500)" strokeWidth="1.2" />
                                    <path d="M5 1v3M11 1v3" stroke="var(--neutral-500)" strokeWidth="1.2" strokeLinecap="round" />
                                </svg>
                                <span className="font-urbanist text-[14px] text-[var(--grey-800)]">{prayerTime.date}</span>
                            </div>
                        ) : (
                            <input
                                type="date"
                                value={date}
                                onChange={(e) => setDate(e.target.value)}
                                className="w-full px-4 py-2.5 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[14px] text-[var(--grey-800)]
                                    focus:outline-none focus:border-[var(--brand)] transition-colors"
                            />
                        )}
                    </div>

                    {/* Tab Switcher — segmented control matching Figma */}
                    <div className="flex mb-6 border-b border-[var(--border-01)]">
                        <button
                            onClick={() => setActiveTab('daily')}
                            className={`flex-1 py-3 font-urbanist font-semibold text-[14px] text-center transition-colors relative cursor-pointer
                                ${activeTab === 'daily'
                                    ? 'text-[var(--brand)]'
                                    : 'text-[var(--neutral-500)] hover:text-[var(--grey-800)]'
                                }`}
                        >
                            Daily Prayer
                            {activeTab === 'daily' && (
                                <span className="absolute bottom-0 left-0 right-0 h-[2px] bg-[var(--brand)] rounded-t-full" />
                            )}
                        </button>
                        <button
                            onClick={() => setActiveTab('jumuah')}
                            className={`flex-1 py-3 font-urbanist font-semibold text-[14px] text-center transition-colors relative cursor-pointer
                                ${activeTab === 'jumuah'
                                    ? 'text-[var(--brand)]'
                                    : 'text-[var(--neutral-500)] hover:text-[var(--grey-800)]'
                                }`}
                        >
                            Jumu&apos;ah
                            {activeTab === 'jumuah' && (
                                <span className="absolute bottom-0 left-0 right-0 h-[2px] bg-[var(--brand)] rounded-t-full" />
                            )}
                        </button>
                    </div>

                    {/* ── Daily Prayer Grid ── */}
                    {activeTab === 'daily' && (
                        <div>
                            {/* Column Headers */}
                            <div className="grid grid-cols-[120px_1fr_1fr] gap-4 mb-2">
                                <div />
                                <p className="font-urbanist font-semibold text-[12px] text-[var(--neutral-500)] uppercase tracking-wider text-center">Begins</p>
                                <p className="font-urbanist font-semibold text-[12px] text-[var(--neutral-500)] uppercase tracking-wider text-center">Jamah</p>
                            </div>

                            {/* Prayer Rows */}
                            {PRAYER_ROWS.map(({ key, label, hasJamah }) => (
                                <div key={key} className="grid grid-cols-[120px_1fr_1fr] gap-4 py-2.5 border-b border-[var(--border-01)] items-center">
                                    <span className="font-urbanist font-semibold text-[13px] text-[var(--brand)] uppercase">{label}</span>
                                    <input
                                        type="time"
                                        value={prayers[key]?.athan || ''}
                                        onChange={(e) => handlePrayerChange(key, 'athan', e.target.value)}
                                        className="w-full px-3 py-2 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[13px] text-[var(--grey-800)] text-center
                                            focus:outline-none focus:border-[var(--brand)] transition-colors"
                                    />
                                    {hasJamah ? (
                                        <input
                                            type="time"
                                            value={prayers[key]?.jamah || ''}
                                            onChange={(e) => handlePrayerChange(key, 'jamah', e.target.value)}
                                            className="w-full px-3 py-2 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[13px] text-[var(--grey-800)] text-center
                                                focus:outline-none focus:border-[var(--brand)] transition-colors"
                                        />
                                    ) : (
                                        <div />
                                    )}
                                </div>
                            ))}
                        </div>
                    )}

                    {/* ── Jumu'ah Tab ── */}
                    {activeTab === 'jumuah' && (
                        <div className="space-y-4">
                            <p className="font-urbanist text-[12px] text-[var(--neutral-500)] italic">
                                *Please note Jumu&apos;ah prayer times are applicable for Fridays only
                            </p>

                            {jumuahTimes.map((slot, index) => (
                                <div key={index} className="p-4 border border-[var(--border-01)] rounded-[10px]">
                                    <div className="flex items-center justify-between mb-3">
                                        <span className="font-urbanist font-semibold text-[14px] text-[var(--brand)]">
                                            JUMU&apos;AH {index + 1}
                                        </span>
                                        {jumuahTimes.length > 1 && (
                                            <button
                                                onClick={() => removeJumuahSlot(index)}
                                                className="text-[var(--error)] font-urbanist text-[12px] hover:underline cursor-pointer"
                                            >
                                                Remove
                                            </button>
                                        )}
                                    </div>
                                    <div className="grid grid-cols-3 gap-4">
                                        <div>
                                            <label className="block font-urbanist text-[12px] text-[var(--neutral-500)] mb-1">Begins</label>
                                            <input
                                                type="time"
                                                value={slot.begins || ''}
                                                onChange={(e) => handleJumuahChange(index, 'begins', e.target.value)}
                                                className="w-full px-3 py-2 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[13px]
                                                    focus:outline-none focus:border-[var(--brand)]"
                                            />
                                        </div>
                                        <div>
                                            <label className="block font-urbanist text-[12px] text-[var(--neutral-500)] mb-1">Khutbah</label>
                                            <input
                                                type="time"
                                                value={slot.khutbah || ''}
                                                onChange={(e) => handleJumuahChange(index, 'khutbah', e.target.value)}
                                                className="w-full px-3 py-2 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[13px]
                                                    focus:outline-none focus:border-[var(--brand)]"
                                            />
                                        </div>
                                        <div>
                                            <label className="block font-urbanist text-[12px] text-[var(--neutral-500)] mb-1">Jamah</label>
                                            <input
                                                type="time"
                                                value={slot.jamah || ''}
                                                onChange={(e) => handleJumuahChange(index, 'jamah', e.target.value)}
                                                className="w-full px-3 py-2 border border-[var(--border-01)] rounded-[8px] font-urbanist text-[13px]
                                                    focus:outline-none focus:border-[var(--brand)]"
                                            />
                                        </div>
                                    </div>
                                </div>
                            ))}

                            <button
                                onClick={addJumuahSlot}
                                className="w-full py-2.5 border border-dashed border-[var(--brand)] text-[var(--brand)] rounded-[8px]
                                    font-urbanist font-semibold text-[13px] hover:bg-[var(--brand-05)] transition-colors cursor-pointer"
                            >
                                + Add Jumu&apos;ah Slot
                            </button>
                        </div>
                    )}

                    {/* Error */}
                    {error && (
                        <p className="mt-4 font-urbanist text-[13px] text-[var(--error)]">{error}</p>
                    )}

                    {/* Note */}
                    <p className="mt-4 font-urbanist text-[12px] italic text-[var(--neutral-500)]">
                        *Whatever the changes saved, it&apos;ll automatically reflect in the Mobile App.
                    </p>
                </div>

                {/* ── Footer ── */}
                <div className="flex items-center justify-end gap-3 px-6 py-5 border-t border-[var(--border-01)]">
                    <button
                        onClick={onClose}
                        disabled={saving}
                        className="px-8 py-2.5 border border-[var(--border-01)] text-[var(--grey-800)] rounded-[8px]
                            font-urbanist font-semibold text-[13px] hover:bg-[var(--neutral-100)] transition-colors disabled:opacity-50 cursor-pointer"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={handleSave}
                        disabled={saving}
                        className="px-8 py-2.5 bg-[var(--brand)] text-white rounded-[8px]
                            font-urbanist font-semibold text-[13px] hover:opacity-90 transition-opacity disabled:opacity-50 cursor-pointer"
                    >
                        {saving ? 'Saving...' : 'Save'}
                    </button>
                </div>
            </div>
        </div>
    );
}
